              text: '–û–¥–Ω–æ–π –∏–∑ –ø—Ä–æ–±–ª–µ–º –∏–Ω—Ç–µ–Ω—Å–∏–≤–Ω—ã—Ö –∫—É–ª—å—Ç—É—Ä —è–≤–ª—è–µ—Ç—Å—è —Å–Ω–∏–∂–µ–Ω–∏–µ –ø—Ä–æ–¥—É–∫—Ç–∏–≤–Ω–æ—Å—Ç–∏ –≤ –ø–µ—Ä–∏–æ–¥ —Å–∏–ª—å–Ω–æ–π –∂–∞—Ä—ã –∏ –∑–∞—Å—É—Ö–∏. –ö–∞–ª–∏–π –≤ –∑–∞—Å—É—à–ª–∏–≤—ã–π –ø–µ—Ä–∏–æ–¥ —Å–ø–æ—Å–æ–±—Å—Ç–≤—É–µ—Ç —Ä–∞–∑–∂–∏–∂–µ–Ω–∏—é –∫–ª–µ—Ç–æ—á–Ω–æ–≥–æ —Å–æ–∫–∞ –∏ –æ—Ç—Ç–æ–∫—É –ø–∏—Ç–∞—Ç–µ–ª—å–Ω—ã—Ö –≤–µ—â–µ—Å—Ç–≤ –≤ –∑–∞–ø–∞—Å–∞—é—â–∏–µ –æ—Ä–≥–∞–Ω—ã.'
            },
            
          ],
          infoBot: [
            {
              class: 'par-ttl',
              text: '–ù–∞–∑–Ω–∞—á–µ–Ω–∏–µ ¬´–ú–ï–ì–ê–ú–ò–ö–° ‚Äì –ö–ê–õ–ò–ô¬ª:'
            },
            {
              class: 'par',
              text: '–£—Å—Ç—Ä–∞–Ω–µ–Ω–∏–µ –ø—Ä–∏–∑–Ω–∞–∫–æ–≤ –∫–∞–ª–∏–π–Ω–æ–≥–æ –≥–æ–ª–æ–¥–∞–Ω–∏—è ‚Äî –∫—Ä–∞–µ–≤–æ–π –æ–∂–æ–≥, —É–≤—è–¥–∞–Ω–∏–µ '
            },
            {
              class: 'par',
              text: '–û–±–µ—Å–ø–µ—á–µ–Ω–∏–µ –∫–∞–ª–∏–π–Ω–æ–≥–æ –ø–∏—Ç–∞–Ω–∏—è –≤¬†–∫—Ä–∏—Ç–∏—á–µ—Å–∫–∏–µ —Ñ–∞–∑—ã —Ä–∞–∑–≤–∏—Ç–∏—è –∫—É–ª—å—Ç—É—Ä—ã (–æ—Å–æ–±–µ–Ω–Ω–æ –ø–µ—Ä–µ–¥ —Ü–≤–µ—Ç–µ–Ω–∏–µ–º) '
            },
            {
              class: 'par',
              text: '–°—Ç–∏–º—É–ª—è—Ü–∏—è —Ä–æ—Å—Ç–æ–≤—ã—Ö –ø—Ä–æ—Ü–µ—Å—Å–æ–≤. –ò–Ω—Ç–µ–Ω—Å–∏–≤–Ω—ã–π —Ä–æ—Å—Ç —á–∞—Å—Ç–æ —Å–æ–≤–ø–∞–¥–∞–µ—Ç —Å¬†–ø–∏–∫–æ–º –ø–æ–≥–ª–æ—â–µ–Ω–∏—è –∫–∞–ª–∏—è (–æ—Å–æ–±–µ–Ω–Ω–æ –Ω–∞¬†–ø—Ä–æ–ø–∞—à–Ω—ã—Ö –∫—É–ª—å—Ç—É—Ä–∞—Ö) '
            },
            {
              class: 'par',
              text: '–ü–æ–≤—ã—à–µ–Ω–∏–µ –≤—ã–∂–∏–≤–∞–µ–º–æ—Å—Ç–∏ —Ä–∞—Å—Ç–µ–Ω–∏–π –ø—Ä–∏ —Å—Ç—Ä–µ—Å—Å–∞—Ö –ø–æ–≥–æ–¥–Ω–æ–≥–æ –∏¬†–∞–Ω—Ç—Ä–æ–ø–æ–≥–µ–Ω–Ω–æ–≥–æ —Ö–∞—Ä–∞–∫—Ç–µ—Ä–∞ '
            },
            {
              class: 'par',
              text: '–ü–æ–≤—ã—à–µ–Ω–∏–µ –∫–æ–ª–∏—á–µ—Å—Ç–≤–∞ –∏ –∫–∞—á–µ—Å—Ç–≤–∞ —É—Ä–æ–∂–∞—è (–æ—Ç—Ç–æ–∫–∞ –∞—Å—Å–∏–º–∏–ª—è—Ç–æ–≤) –ø—Ä–∏ –∑–∞—Å—É—Ö–µ'
            },
          ],
          video: false,
          table: "/static/tara/tables/kaliy-tbl.png"
        },
      ],
      i: null
    }
  },
  mounted(){
    this.i = this.$route.params.id
    this.goods[this.i].bold = true;
  },
  methods:{
    chooseProd(val){
      this.goods[this.i].bold = false;
          //this.$router.push('/products/'+val)
          this.i = val
          this.goods[this.i].bold = true;
    },
    goTo(val){
      this.$router.push(val)
    }
  }
}
</script>

<style>
.product{
    padding: 0 5%;
}
.product_block-img{
  margin-bottom: 20px;
  width: 100%;
}
.product-info{
  position: relative;
  width: 100%;
  position: relative;
  vertical-align: top;
  display: inline-flex;
}
.main__block2{
    width: 66%;
    display: inline-block;
}
.product__menu{
    width: 33%;
    display: inline-block;
    text-align: left;
    padding: 15px 0 0 60px;
}
  .product__menu-item-img{
      display: inline-block;
  }
  .product__menu-item-name{
      display: inline-block;
  }
  .product__menu-title{
    margin-bottom: 20px;
  }
  .product__block-info-discription{
      text-align: justify;
      font-size: 16px;
  }
  .product-img{
      width: 70px;
      height: 70px;
      margin-bottom: 20px;
  }
  .tbl-elem{
      width: 100%;
      margin: 30px 0 30px 0;
  }
  .par-ttl{
      font-size: 24px;
      margin-bottom: 20px;
  }
  .par{
    margin-bottom: 20px;
  }
  .product__video{
    position: relative;
    width: 100%;
    display: inline-flex;
    margin: 60px 0;
  }
  .product__video-block{
    width: 50%;
    text-align: left;
  }
  .product__video-title{
    margin-bottom: 20px;
  }
  .tbl-elem__img{
    width: 100%;
  }
  .bold{
    font-weight: 500;
  }
</style>



