            },

            {
              class: 'par',
              text: '–ë–ª–∞–≥–æ–¥–∞—Ä—è —Å–≤–æ–π—Å—Ç–≤—É –∫–∞–ª–∏—è —Å–≤—è–∑—ã–≤–∞—Ç—å –∏¬†—Å—Ç—Ä—É–∫—Ç—É—Ä–∏—Ä–æ–≤–∞—Ç—å –≤–æ–¥—É –≤ –∫–ª–µ—Ç–∫–∞—Ö —Ä–∞—Å—Ç–µ–Ω–∏—è –æ–±—Ä–∞–±–æ—Ç–∫–∏ ¬´–ú–ï–ì–ê–ú–ò–ö–° ‚Äì –ö–ê–õ–ò–ô¬ª —Å–Ω–∏–∂–∞—é—Ç –ø–æ–≤—Ä–µ–∂–¥–µ–Ω–∏–µ –º–æ—Ä–æ–∑–∞–º–∏ –Ω–∞ –ø–µ—Ä–µ—Ä–æ—Å—à–∏—Ö –æ–∑–∏–º—ã—Ö –∫—É–ª—å—Ç—É—Ä–∞—Ö –∏ —Å–ø–æ—Å–æ–±—Å—Ç–≤—É—é—Ç —É–¥–µ—Ä–∂–∞–Ω–∏—é –≤–ª–∞–≥–∏ –ø—Ä–∏ –∑–∞—Å—É—Ö–µ. '
            },
            {
              class: 'par',
              text: '–û–¥–Ω–æ–π –∏–∑ –ø—Ä–æ–±–ª–µ–º –∏–Ω—Ç–µ–Ω—Å–∏–≤–Ω—ã—Ö –∫—É–ª—å—Ç—É—Ä —è–≤–ª—è–µ—Ç—Å—è —Å–Ω–∏–∂–µ–Ω–∏–µ –ø—Ä–æ–¥—É–∫—Ç–∏–≤–Ω–æ—Å—Ç–∏ –≤ –ø–µ—Ä–∏–æ–¥ —Å–∏–ª—å–Ω–æ–π –∂–∞—Ä—ã –∏ –∑–∞—Å—É—Ö–∏. –ö–∞–ª–∏–π –≤ –∑–∞—Å—É—à–ª–∏–≤—ã–π –ø–µ—Ä–∏–æ–¥ —Å–ø–æ—Å–æ–±—Å—Ç–≤—É–µ—Ç —Ä–∞–∑–∂–∏–∂–µ–Ω–∏—é –∫–ª–µ—Ç–æ—á–Ω–æ–≥–æ —Å–æ–∫–∞ –∏ –æ—Ç—Ç–æ–∫—É –ø–∏—Ç–∞—Ç–µ–ª—å–Ω—ã—Ö –≤–µ—â–µ—Å—Ç–≤ –≤ –∑–∞–ø–∞—Å–∞—é—â–∏–µ –æ—Ä–≥–∞–Ω—ã.'
            },
            
          ]
