                    <div class="par">–ü—Ä–µ–¥–ø–æ—Å–µ–≤–Ω–∞—è –æ–±—Ä–∞–±–æ—Ç–∫–∞ —É–¥–æ–±—Ä–µ–Ω–∏–µ–º –ú–ï–ì–ê–ú–ò–ö–° —Å–Ω–∏–∂–∞–µ—Ç —Å—Ç—Ä–µ—Å—Å–æ–≤–æ–µ –≤–æ–∑–¥–µ–π—Å—Ç–≤–∏–µ –ø–µ—Å—Ç–∏—Ü–∏–¥–æ–≤, –ø–æ–≤—ã—à–∞–µ—Ç –ø–æ–ª–µ–≤—É—é –≤—Å—Ö–æ–∂–µ—Å—Ç—å –Ω–∞ 10‚Äì15% –∏ –∫—É—â–µ–Ω–∏–µ –Ω–∞ 25‚Äì45%, —É–≤–µ–ª–∏—á–∏–≤–∞–µ—Ç —É—Å—Ç–æ–π—á–∏–≤–æ—Å—Ç—å –∫ –ø–æ–ª–µ–≥–∞–Ω–∏—é, –ø–æ–≤—ã—à–∞–µ—Ç –∏–º–º—É–Ω–∏—Ç–µ—Ç, –∑–∞—Å—É—Ö–æ—É—Å—Ç–æ–π—á–∏–≤–æ—Å—Ç—å, —É–≤–µ–ª–∏—á–∏–≤–∞–µ—Ç –ø–æ—Ç–µ–Ω—Ü–∏–∞–ª –∫—É–ª—å—Ç—É—Ä—ã –ø–æ —É—Ä–æ–∂–∞–π–Ω–æ—Å—Ç–∏ –∏ –∫–∞—á–µ—Å—Ç–≤—É. –ü—Ä–µ–¥–ø–æ—Å–µ–≤–Ω–∞—è –æ–±—Ä–∞–±–æ—Ç–∫–∞ —Å–µ–º—è–Ω —Å–ø–æ—Å–æ–±—Å—Ç–≤—É–µ—Ç –ø–æ–≤—ã—à–µ–Ω–∏—é –∑–∏–º–æ—Å—Ç–æ–π–∫–æ—Å—Ç–∏ —Ä–∞—Å—Ç–µ–Ω–∏–π –∑–∞ —Å—á–µ—Ç —É–≤–µ–ª–∏—á–µ–Ω–∏—è –∫–æ—Ä–Ω–µ–≤–æ–π —Å–∏—Å—Ç–µ–º—ã –∏ –Ω–∞–∫–æ–ø–ª–µ–Ω–∏—è –Ω–∏–∑–∫–æ–º–æ–ª–µ–∫—É–ª—è—Ä–Ω—ã—Ö –º–µ—Ç–∞–±–æ–ª–∏—Ç–æ–≤: </div>
